@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Stencil+Display&family=Cinzel:wght@500&family=Gloria+Hallelujah&family=Modak&family=Pacifico&family=Pangolin&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}

.App {
  background-color: #141414;
  min-height: 100vh;
}

.nav {
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 20px 50px;
    z-index: 1;
    height: 40px;
    background: linear-gradient(
        180deg,
        #141414c2,
        #1414148e 40%,
        transparent
    );
    transition: all 0.5s;
    transition-timing-function: ease-out;
}

.nav-black {
    background-color: #141414;
    transition: 400ms;
}

.logo {
    position: fixed;
    width: 110px;
    object-fit: contain;
    padding-top: 5px;
}

.avatar {
    position: fixed;
    width: 40px;
    object-fit: contain;
    right: 20px;
}

.icons {
    position: fixed;
    right: 70px;
}

.icon {
    height: 20px;
    padding: 10px;
}
.menu {
    position: fixed;
    left: 170px;
    display: flex;
}

.menu-link{
    margin: 10px;
    font-size: calc(0.5rem + 0.5vw);
    color: white;
    text-decoration: none;
}
.dropdown {
    position: fixed;
    left: 170px;
    outline: none;
    padding: auto;
    top: 28px;
}

label {
    margin-right: 5px;
}

select {
    outline-style: none;
}

option {
    color: rgb(187, 187, 187);
    background-color: black;
}

option:hover {
    color: black;
}
html {
  color: white;
}

.title {
  font-size: 1.5rem;
  padding-bottom: 5px;
  font-family: Arial, Helvetica, sans-serif;
}

.row {
  margin-left: 50px;
}

.poster-row {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
}

.poster-row::-webkit-scrollbar {
  display: none;
}

.poster {
  object-fit: contain;
  width: 100%;
  max-height: 15vh;
  transition: transform 500ms;
  border: 1px solid transparent;
  border-radius: 10px;
  padding: 5px;
}

.poster_lrg {
  max-height: 30vh;
  object-fit: contain;
  width: 100%;
  transition: transform 500ms;
  padding: 5px;
  border: 1px solid transparent;
  border-radius: 15px;
}

.poster:hover {
  transform: scale(1.05);
  cursor: pointer;
}

.poster_lrg:hover {
  transform: scale(1.08);
  cursor: pointer;
}



.billboard {
  height: 700px;
  object-fit: contain;
}

.billboard-description {
  margin-left: 50px;
  padding-top: 200px;
  width: 35%;
  height: 445px;
  overflow: scroll;
}

.billboard-description::-webkit-scrollbar {
    display: none;
}

h2 {
  font-size: 2.5rem;
}

.billboard-buttons {
  display: flex;
}

.button {
  margin: 5px;
  padding: 15px;
  border-radius: 5px;
  border-color: transparent;
  display: flex;
}

.billboard-buttons :hover {
    cursor: pointer;
    opacity: 0.7;
}

.button img {
  max-height: 1.2rem;
  padding: 0px 10px;
}

.play-button {
  background-color: white;
  min-width: 130px;
  font-size: 1.2rem;
  font-weight: 600;
  outline: none;
}

.play-button:focus {
  opacity: 1;
}

.info-button:focus {
  opacity: 1;
}

.info-button {
  background-color: rgba(109, 109, 110, 0.7);
  color: white;
  white-space: nowrap;
  min-width: 170px;
  font-size: 1.2rem;
  font-weight: 600;
  outline: none;
}

@media screen and (max-width: 970px) {
  .billboard-description {
    width: 50%;
    padding-top: 150px;
    height: 495px;
  }

  .overview {
    max-height: 380px;
  }
}

@media screen and (max-width: 650px) {
  .billboard-description {
    width: 80%;
    padding-top: 100px;
    height: 555px;
  }
}

@media screen and (max-width: 450px) {
  .overview {
    max-height: 300px;
  }
}

.overview {
    overflow: scroll;
}

.overview::-webkit-scrollbar {
  display: none;
}

p {
  font-size: 1.2rem;
  font-weight: bold;
  margin: 10px;
  font-family: sans-serif;
}

.bottom-fade {
    bottom: 0;
    height: 3.5rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37, 0.61),
        #141414
    );
    
}
